*,*::before,*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

%pos-abs-tl{
  position: absolute;
  top: 0;
  left: 0;
}

.header{
  padding: 0 15px;
  height: 53px;
  width: 100%;
  background: #0a71c6;
}

.header .holdings{
  display: flex;
  justify-content: space-between;
  padding: 8px 16px;
  position: relative;
}

.header .holdings .sociallinks ul a{
  text-decoration: none;
  color: whitesmoke;
}

.sociallinks ul a i{
  margin: 5px;
  line-height: 20px;
  padding: 5px 10px;
  font-size: 20px;
  border-radius: 5%;
  transition: .2s linear;
}

.sociallinks ul a i:hover{
  background-color: #06508b;
  border-bottom: 2px solid whitesmoke;
}

.holdings .show-menu-btn{
  display: none;
}

.header .holdings .routes nav ul{
  display: flex;
}

.holdings .routes nav ul li, .holdings .routes nav ul li a{
  list-style: none;
  text-decoration: none;
}

.routes nav ul li a{
  color: rgba(255,255,255,0.5);
  font-size: 14px;
  margin: 3px 5px;
  padding: 5px;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  transition: .2s linear;
  position: relative;
}
.drop-menu{
  display: none;
}

.routes nav ul li{
  padding: 8px;
  line-height: 20px;
  font-weight: 550;
}

.routes nav ul li .active{
  color: white;
  font-weight: 600;
}

.routes nav ul li .active:hover{
  color: white;
}

.routes nav ul li a:hover{
  color: rgba(255,255,255,0.75);
  background-color: #06508b;
  border-radius: 5%;
  border-bottom: 2px solid rgba(241, 243, 241, 0.774);
}

.container{
  background: rgba(14, 146, 223, 0.719);
  width: 100%;
  height: 340px;
}

.content{
  display: flex;
  justify-content: space-evenly;
  padding: 48px 0;
  margin: 0 90px;
  padding: 48 10px;
}

.content .img-side .blue-box{
  background: blanchedalmond;
  width: 240px;
  height: 240px;
  margin: 0 48px;
  border-radius: 3px;
}

.content .info-side .intro{
  color: rgb(245, 245, 245);
  font-size: 20px;
  font-family: 'Roboto', sans-serif;
  font-weight: lighter;
}

.fname{
  font-size: 56px;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: white;
  margin: 0 0 8px;
}
.text-info{
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  margin: 0 0 25px;
  line-height: 25px;
  color: whitesmoke;
}
.text-info a{ 
  text-decoration: none;
}

.hire-btn{
  text-decoration: none;
  padding: 10px 24px;
  color: whitesmoke;
  font-weight: bold;
  background: #06508b;
}

@media screen and (max-width: 1100px){
  .routes{
    display: none;
  }
  .show-menu-btn{
    display: block;
    font-size: 50px;
  }
}